import { PrintLotModule } from './../../../../components/print-lot/print-lot.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { OpeningStockComponent } from './opening-stock.component';
import { AddOpeningStockComponent } from './add-opening-stock/add-opening-stock.component';
import { DetailComponent } from './detail/detail.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { CanLeavePage } from 'src/app/canLeavePage';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { CreateOpeningDataComponent } from './create-opening-data/create-opening-data.component';
import { BillType9Module } from 'src/app/sales/components/bill-type/bill-type-9/bill-type9.module';
import { EditOpeningStockComponent } from './edit-opening-stock/edit-opening-stock.component';
const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: "",
        component: OpeningStockComponent
      },
      {
        path: "detail",
        component: DetailComponent
      },
      {
        path: "create-opening-data",
        component: CreateOpeningDataComponent
      },
      {
        path: "add-opening-stock",
        component: AddOpeningStockComponent,
      
      },
      {
        path: "edit",
        component: EditOpeningStockComponent
      },

    ]
  }
]

@NgModule({
  declarations: [OpeningStockComponent, CreateOpeningDataComponent, AddOpeningStockComponent, DetailComponent, EditOpeningStockComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    TranslateModule,
    ScrollingModule,
    ReactiveFormsModule,
    BillType9Module,
    PrintLotModule
  ]
})
export class OpeningStockModule { }
