import { Component, OnInit, HostListener, ChangeDetectorRef, ViewChild, ElementRef } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { PurchaseA4Component } from 'src/app/sales/components/print/purchase/purchase-a4/purchase-a4.component';
import { PurchaseA5Component } from 'src/app/sales/components/print/purchase/purchase-a5/purchase-a5.component';
import { PurchaseK80Col4Component } from 'src/app/sales/components/print/purchase/purchase-k80-col4/purchase-k80-col4.component';
import { PurchaseK80Col3Component } from 'src/app/sales/components/print/purchase/purchase-k80-col3/purchase-k80-col3.component';
import { PurchaseK57Col3Component } from 'src/app/sales/components/print/purchase/purchase-k57-col3/purchase-k57-col3.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhBillType9Add, VhBillType9DetailAdd } from '../bill-type9-interface';
@Component({
  selector: 'bill-type9-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

  invoice: VhBillType9Add = {
    bill_type: 9,
    id_branch: this.vhQuerySales.getDefaultBranch()._id,
    id_employee: this.vhAuth.getUser()._id,
    date: new Date(),
    total: 0,
    tax: 0,
    fee: 0,
    discount: 0,
    payment: 0,
    note: '',
  }
  invoice_detail: VhBillType9DetailAdd[] = []
  subTotal = 0;
  taxValue = 0
  listTax: any[] = this.vhQuerySales.getlocalTaxs().filter(tax => tax.status);
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  employee: any = {};
  printer: any;
  listBarcodeLot = [];
  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private vhAuth: VhAuth,
    private fncService: FunctionService,
    private cdRef: ChangeDetectorRef,
    private vhQuery: VhQuery,
    private nzModalService: NzModalService
  ) {

  }

  ngOnInit(): void {
    this.invoice.date = this.vhAuth.getStore().registrationdate
    this.employee = this.vhAuth.getlocalEmployee(this.invoice.id_employee);
    // ------bt s kin nhn phm nng-----
    this.eventKeypress();
    let tax = this.listTax.find(item => item.default);
    if (tax) {
      this.id_tax = tax._id
      this.invoice.tax = (tax['value']);
    }
    let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_purchase')
    this.printer = printer ? printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)] : { discount_hidden: false, note_hidden: false, fee_hidden: false, tax_hidden: false, debt_hidden: false };

    // get d liu in l
    this.vhQuery.getDocsByFields("barcode_designs", { id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id }, type: { $eq: 5 } })
      .then((response: any) => {
        

        this.listBarcodeLot = response.map(item => { item.property = this.vhAlgorithm.sortNumberbyASC(item.property, 'index'); return { ...item } })
      })
  }
  event: any // bin lng nghe
  ngOnDestroy() {
    document.removeEventListener('keydown', this.event, false);
  }
  checkPrinter: boolean = false;

  /**
   * lng nghe sk nhn f1 f2 f3 f4 trn bn phm
   * @example 
   */
  eventKeypress() {
    this.event = (event: any) => {
      const keycode = event.keyCode;
      if (keycode === 112 || keycode === 113 || keycode === 114) event.preventDefault();
      if (keycode === 115) this.searchProductList = !this.searchProductList;
      else if (this.invoice_detail.length > 0) {
        switch (keycode) {

          case 112: this.payInvoice(9, false)
            break;
          case 113: {
            if (this.checkPrinter) this.payInvoice(9, true)
            break;
          }
        }
      }
    }
    document.addEventListener('keydown', this.event, false);
  }
  ngAfterViewInit() {
    this.checkPrinter = this.printer.enable;
  }

  ngAfterViewChecked() {

    this.cdRef.detectChanges();
  }
  /**
   *  hm xa sp
   * @param detail
   * @example  this.deleteGoods(bill_detail_selected)
   */
  deleteGoods(detail: VhBillType9DetailAdd, index?) {
    this.vhComponent.alertConfirm("", "", `${this.languageService.translate("Delete")} ${detail.name}?`, "OK", this.languageService.translate("Cancel"))
      .then(() => {
        if (!this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').show_product_mutil_line_purchase)
          this.removeInvoiceDetail(detail.id_product)
        else
          this.invoice_detail.splice(index, 1);
        this.setSubTotal();
      }, () => { })
  }
  searchProductList: boolean = false;
  /**
 * m component chn sp
 * @example this.openSearchProductList()
 */
  openSearchProductList() {
    this.searchProductList = true;
  }
  /**
   * nhn output t component chn sp (app-search-product-import)
   * @param data sp c chn  thm vo bill
   * @example this.closeSearchProductList(product_selected)
   */
  closeSearchProductList(data) {
    if (data) {
      

      if (data.manylot && !data.id_lotproduct) this.openModalChooseLot(data)
      else {
        if (!this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').show_product_mutil_line_purchase)
          this.addInvoiceDetail(data);
        else
          this.addInvoiceDetailMutilLine(data);
      }

    }
    else
      this.searchProductList = false;

  }
  /**
   * tr v trang danh sch ha n
   * @example this.goBack() 
   */
  goBack() {
    this.vhComponent.alertConfirm(`${this.languageService.translate("Confirm")}`, "", `${this.languageService.translate("Are you sure to cancel the purchase order")}?`, "OK", this.languageService.translate('Cancel'))
      .then(() => { this.isExit = true; this.router.navigate(["/sales/dashboard/manage/invoice/opening-stock"], { state: this.dataRestore }) }, () => { })
  }
  /**
   * bt sk thay i s lng sp trong bill
   * @param product 
   * @param event : gi tr thay i
   * @example this.checkQuantityProduct(bill_detail_selected, 1)
   */
  checkQuantityProduct(product: VhBillType9DetailAdd, event) {
    if (!event) product.quantity = 1;
    this.setSubTotal();
  }

  /**
   * bt sk thay i n v ca bill_detail
   * @param ev  ratio c chn
   * @param item
   * @example  this.changeUnit(24, bill_detail_selected)
   */
  changeUnit(ev, item) {
    let unit = this.vhQuerySales.getUnit_byRatio(item.units, ev);
    if (unit) {
      item.unit = unit.unit
      item.price = parseFloat(unit.price_import)
      item.ratio = parseFloat(unit.ratio)
      item.price_origin = parseFloat(unit.price_import)
    }
    this.setSubTotal()
  }

  // -------modal edit unit price-------
  isVisibleEditUnitPrice: boolean = false;
  unit_price: any;
  tempDetail: VhBillType9DetailAdd;
  /**
  * m modal thay i gi bn sn phm trong bill
  * @param detail 
  * @example this.openEditUnitPrice(bilL_detail_selected)
  */
  openEditUnitPrice(detail: VhBillType9DetailAdd) {
    this.tempDetail = detail;
    this.isVisibleEditUnitPrice = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.unit_price = this.vhAlgorithm.vhnumeral('.unit_price');
    });
  }
  /**
   * bt sk nhn nt Ok modal gim gi sp
   * @example this.handleEditUnit()
   */
  handleEditUnit() {
    if (this.unit_price.getRawValue() != '' && this.unit_price.getRawValue() >= 0) {
      this.tempDetail.price = parseFloat(this.unit_price.getRawValue());
      this.setSubTotal();
      this.isVisibleEditUnitPrice = false;
    } else { this.vhComponent.showToast(2000, this.languageService.translate("Invalid data, please check again"), "alert-toast") }
  }
  /**
   * tt modal gim gi sp
   * @example this.handleCancelEditUnit()
   */
  handleCancelEditUnit() { this.isVisibleEditUnitPrice = false }
  // -------set employee-------
  /**
  * bt sk thay i nv bn hng
  * @param event id_employee selected
  */
  closeAndReceiveEmployee(event) {
    if (event) {
      this.invoice.id_employee = (event);
    }
  }

  // -------change tax------
  id_tax: string;
  /**
   * bt sk thay i thu v cp nht li trong bill local
   * @param event id_tax selected
   * @example this.changeTax(10)
   */
  changeTax(event) {
    let tax = this.listTax.find(item => item._id == event);
    this.invoice.tax = (tax.value);
    this.setTaxValue();
  }


  /**
   * tr v trang danh sch ha n sau khi  thanh ton xong
   * @example this.handle()
   */
  handle() {
    this.isExit = true;
    this.router.navigate(['/sales/dashboard/manage/invoice/opening-stock'], { state: this.dataRestore })
  }
  note: string;
  /**
  * bt sk thay i note ca bill
  * @example this.edit_note()
  */
  edit_note() {
    if (this.note) {
      this.invoice.note = (this.note)
    }
  }
  save_enable = true;
  /**
    * x l sk nhn nt thanh ton hoc lu tm
    * @param bill_type 
    * @param print bin c  check in hay ko
    * @example this.payInvoice(9)
    */
  payInvoice(bill_type: 9, print?) {
    this.invoice.payment = this.invoice.total
    if (this.vhAuth.checkMyPermission("purchase_enable_payment")) {
      this.vhComponent.alertConfirm(
        this.languageService.translate("Save") + "?",
        "", "", "OK", this.languageService.translate("Cancel")
      ).then(() => {
        if (this.save_enable) {
          this.save_enable = false;
          this.vhComponent.showLoadingNotDuration("", "transparent-loading").then(() => {
            var arrayProcess: any[] = [this.createOrUpdate(bill_type)];

            // if (bill_type == 2)
            //   this.updateProduct(this.invoice_detail.filter(item => item.price != item.price_origin), arrayProcess)

            Promise.all(arrayProcess).then(bill => {
              this.vhComponent.hideLoading(0).then(() => {
                this.vhComponent.showToast(2000,
                  `${this.languageService.translate("Purchase invoice")} ${bill[0].bill_code || this.invoice.bill_code} ${this.languageService.translate("has been created successfully")}`, "success-toast"
                )
                if (print) this.handlePrint()
                else this.handle()
              })
            })
          })
        }
      }, () => { })
    }
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  /**
   * to bill ln DB
   * @param bill_type 
   * @returns 
   */
  createOrUpdate(bill_type: 9) {
    return this.vhQuerySales.createBill_Billdetail(this.getCreateUpdateInvoice(bill_type), this.getCreateUpdateInvoiceDetail())
  }


  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_purchase')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }
  /**
   * kim tra d liu thng tin my in v m component in
   * @example this.handlePrint()
   */
  handlePrint() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer, invoice: this.invoice,invoice_detail: this.invoice_detail, }, false, false, `modal-print-${printer['_id']}`).then((modal) => {
          modal.onWillDismiss().then(() => this.handle())
        })
    }, (err) =>
      this.vhComponent.alertMessage(this.languageService.translate("Function"), "", this.languageService.translate("Please turn on the printer function"), "OK")
        .then(() => this.handle())
    )
  }
  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return (PurchaseK57Col3Component)
      case 'print_size_k80_3c': return (PurchaseK80Col3Component)
      case 'print_size_k80_4c': return (PurchaseK80Col4Component)
      case 'print_size_a5': return (PurchaseA5Component)
      case 'print_size_a4': return (PurchaseA4Component)
      default: return (PurchaseA4Component)
    }
  }
  // ------thot trang-----
  isExit: boolean = false;
  barcode: string = ''
  barcode_array = [];
  @HostListener("window: keypress", ["$event"])
  /**
   * S kin nhn barcode
   */
  getKeyUp(_event: KeyboardEvent) {
    if (this.router.url === "/sales/dashboard/manage/invoice/opening-stock/add-opening-stock") {
      // if (!this.showSearchBar && !this.showPayInfo) {
      if (_event.key == "Enter") {
        this.barcode_array.forEach((i, index) => {
          if (i == "Shift") {
            this.barcode_array[index + 1] = this.barcode_array[
              index + 1
            ].toUpperCase();
            this.barcode_array.splice(index, 1);
          }
        });
        // this.scanBarcode(this.barcode_array.join(""));
        this.barcode = this.barcode_array.join("");
        setTimeout(() => {
          

          if (this.barcode) {
            let detailproduct = this.vhQuerySales.getlocalDetailProduct_byBarcode(this.barcode);
            if (detailproduct) {
              this.closeSearchProductList({ ...detailproduct, ...this.vhQuerySales.getUnit_byRatio(detailproduct.units, detailproduct.ratio) });
            }
            else {
              this.quantity?.nativeElement.blur();
              if (this.barcode.length > 3) this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Not found"))
            }
          } else
            this.quantity?.nativeElement.blur();
          this.barcode_array = [];
        }, 100);

      } else {
        this.barcode_array.push(_event.key);
      }
      // }
    }
  }



  show_modal_select_lots = false;
  show_modal_add_lot = false;
  formAddLot: FormGroup;
  disabledAddLot = false;
  list_search_lot = [];

  /**
   * tr v tn l theo id_lot c chn
   * @param lots 
   * @param _id 
   * @returns string
   * @example this.getLotNumber([], 'DJWOIDHIEUGHWDSJDNWIHD')
   */
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id)
    return lot.lot_number
  }
  /**
   * lc danh sch l ca sp theo s l (lot_number)
   * @param value 
   */
  searchLot(value) {
    this.tempDetail.lots = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value), this.list_search_lot, ['lot_number'])
  }
  /**
   * m modal chn l cho sp
   * @param data 
   * @example this.openModalChooseLot(bill_detail_selected)
   */
  openModalChooseLot(data) {
    this.show_modal_select_lots = true;
    this.tempDetail = data
    this.list_search_lot = this.tempDetail.lots
  }

  /**
   * bt sk thay i id_lotnumber ca bill-detail
   * @param e id_lotnumber thay i
   * @example this.changeLot('DNIUHWIDCHBIQOHSDOIHW')
   */
  changeLot(e) {
    this.tempDetail.lot_number = this.tempDetail.lots.find(item => item._id == e).lot_number
  }

  /**
   * bt sk nhn nt ok ca modal chn l sp
   * @example this.handleChooseLot()
   */
  handleChooseLot() {
    
    this.tempDetail.lots = this.list_search_lot;
    if (!this.tempDetail.id_product) this.closeSearchProductList(this.tempDetail);
    this.show_modal_select_lots = false;
  }
  /**
     * tr v khong cch gia 2 ngy 
     * vd : getNumberOfDay(15/02/2022, 10/02/2022) => return 5
     * */
  getNumberOfDay(date1, date2) {
    return (date1.getTime() - date2.getTime()) / (24 * 3600 * 1000) > 0 ? (date1.getTime() - date2.getTime()) / (24 * 3600 * 1000) : 0
  }


  /**
   * bt sk nhn nt hy modal chn l sp
   * @example this.handleCancelChooseLot()
   */
  handleCancelChooseLot() {
    this.show_modal_select_lots = false;
    this.tempDetail.lots = this.list_search_lot
  }

  /**
   * m modal thm l sn phm
   */
  openAddLot() {
    this.disabledAddLot = false;
    this.show_modal_add_lot = true;
    this.formAddLot = new FormGroup({
      date_mfg: new FormControl(new Date(new Date().setHours(0, 0, 0,)), Validators.compose([Validators.required])),
      date_exp: new FormControl(new Date(new Date().setHours(23, 59, 59,)), Validators.compose([Validators.required])),
      lot_number: new FormControl('', Validators.compose([Validators.required])),
      hidden: new FormControl(false),
      barcode: new FormControl(''),
    })
    this.vhAlgorithm.waitingStack().then(() => {
      document.getElementById('lotNumberError').style.display = 'none';
    })
  }

  exist_barcode = false
  /**
   * kim tra barcode  form to c trng hay ko
   * @example this.checkBarcode()
   */
  checkBarcode() {
    let detailproduct = this.vhQuerySales.getlocalDetailProduct_byBarcode(this.formAddLot.value.barcode);
    if (detailproduct) {
      this.exist_barcode = true;
      this.disabledAddLot = true;
    }
    else {
      this.disabledAddLot = false;
      this.exist_barcode = false;
    }
  }

  /**
   * bt sk nhn nt ok ca modal to l sp, dng cc trng trong form  to l ln DB
   * @example this.handleAddLot()
   */
  handleAddLot() {
    if (!this.fncService.checkExitsLotNumber(this.formAddLot.value.lot_number)) {
      this.show_modal_add_lot = false;
      
      this.tempDetail.lots = this.tempDetail.lots.concat(this.formAddLot.value)
      if (this.tempDetail.id_subproduct) {
        this.vhQuerySales.addLotProduct(this.tempDetail.id_subproduct, this.formAddLot.value)
          .then((lot: any) => {
            setTimeout(() => {
              this.tempDetail.lots[this.tempDetail.lots.length - 1]._id = lot
              this.tempDetail.lot_number = this.formAddLot.value.lot_number;
              
              this.tempDetail.id_lotproduct = lot
              this.list_search_lot = this.tempDetail.lots
            }, 200);
          }, error => {
            
          })
      }
      else {
        this.vhQuerySales.addLotProduct(this.tempDetail.id_product, this.formAddLot.value)
          .then((lot: any) => {
            setTimeout(() => {
              this.tempDetail.lots[this.tempDetail.lots.length - 1]._id = lot
              this.tempDetail.lot_number = this.formAddLot.value.lot_number;
              
              this.tempDetail.id_lotproduct = lot
              this.list_search_lot = this.tempDetail.lots
            }, 200);

          }, error => {
            
          })
      }
    }
    else {
      document.getElementById('lotNumberError').style.display = 'block';
      this.vhComponent.alertMessageDesktop('error', this.languageService.translate("S l ny  tn ti"))
    }
  }
  product_selected: any = {};
  start_print = false;
  data_print: any;
  /**
   * in tem l
   * @param item product_selected
   * @example  this.printLabel(product_selected)
   */
  printLabel(item) {
    let product = this.vhQuerySales.getlocalDetailProduct(item.id_subproduct ? item.id_subproduct : item.id_product)
    let lot_data = product.lots.find(e => e._id == item.id_lotproduct)
    this.product_selected = product
    let barcode_selected = this.listBarcodeLot.find(e => e._id == product.id_design_barcode_lot)
    if (barcode_selected) {
      this.start_print = true;
      let data = {
        formProduct: {
          name: this.product_selected.name,
          ...lot_data
        },
        barcode: barcode_selected,
        col: this.product_selected.number_column_lot,
        number: this.product_selected.number_print_lot
      }
      
      this.data_print = data
      setTimeout(() => {
        this.start_print = false;
      }, 2000);
    }
    else {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Bn cha chn mu tem cho sn phm ny!"))
    }
  }


  /**
   * tr v tng tin 
   * @returns number
   * @example let total = this.getTotalATax()
   */
  getTotalATax(): number {
    return this.invoice.total + this.taxValue
  }

  /**
   * xa 1 bill_detail trong local
   * @param id id_bill_detail selected
   * @param notSetSub 
   */
  removeInvoiceDetail(id: string, notSetSub?: boolean) {
    let index = this.invoice_detail.findIndex(detail => detail['id_product'] == id)
    if (index != -1) {
      this.invoice_detail.splice(index, 1);
      if (!notSetSub) this.setSubTotal()
    }
  }

  /**
   * tnh li cng tin ca bill khi c s thay i (thm xa sa bill_detail, i gi bn, khuyn mi, ph ,thu ,...)
   * @example this.setSubTotal()
   */
  setSubTotal() {
    this.subTotal = this.invoice_detail.reduce((prev: number, next) => prev + next.quantity * next.price, 0);
    this.setTotal();
  }

  /**
   * tnh li tng tin ca bill  lu trng total
   * @example this.setTotal();
   */
  setTotal() {
    this.invoice.total = this.subTotal
    this.setTaxValue()

  }

  /**
   * tnh gi tr thu ra tin
   * @example this.setTaxValue()
   */
  setTaxValue() {
    this.taxValue = this.invoice.total * (this.invoice.tax / 100)

  }
  /**Thm sn phm v bill detail */
  public addInvoiceDetail(value: any) {
    if (value['id_subproduct']) {
      if (value.lot_number) {
        let index = this.invoice_detail.findIndex(detail => detail['lot_number'] == value['lot_number'] && detail['id_lotproduct'] == value['id_lotproduct'])
        if (index != -1) {
          if (this.invoice_detail[index]['quantity'] < 9999999999) this.invoice_detail[index]['quantity'] += 1
          this.setSubTotal()
        } else {
          this.addBill_Detail(value)
        }
      }
      else {
        let index = this.invoice_detail.findIndex(detail => detail['id_subproduct'] == value['id_subproduct'] && detail['ratio'] == value['ratio'])
        if (index != -1) {
          if (this.invoice_detail[index]['quantity'] < 9999999999) this.invoice_detail[index]['quantity'] += 1
          this.setSubTotal()
        } else {
          this.addBill_Detail(value)
        }
      }
    }
    else {
      if (value.lot_number) {
        let index = this.invoice_detail.findIndex(detail => detail['lot_number'] == value['lot_number'] && detail['id_lotproduct'] == value['id_lotproduct'])
        if (index != -1) {
          if (this.invoice_detail[index]['quantity'] < 9999999999) this.invoice_detail[index]['quantity'] += 1
          this.setSubTotal()
        } else {
          this.addBill_Detail(value)
        }
      }
      else {
        let index = this.invoice_detail.findIndex(detail => detail['id_product'] == value['_id'] && detail['ratio'] == value['ratio'])
        if (index != -1) {
          if (this.invoice_detail[index]['quantity'] < 9999999999) this.invoice_detail[index]['quantity'] += 1
          this.setSubTotal()
        } else {
          this.addBill_Detail(value)
        }
      }
    }


  }

  /**
     * x l cc trng ca sp c chn v thm vo mng local
     * @param value 
     * @example  this.addBill_Detail(product_selected)
     */
  addBill_Detail(value) {
    this.invoice_detail.unshift({
      name: value.name,
      id_product: value._id,
      quantity: 1,
      unit: value.unit,
      units: value.units,
      ratio: value.ratio ? value.ratio : 1,
      ptype: value.type,
      price: value.price_import,
      price_origin: value.price_import,
      id_subproduct: value.id_subproduct,
      lots: value.lots,
      lot_number: value.lot_number,
      id_lotproduct: value.id_lotproduct,
    })

    if (this.auto_active_quantity_input) {
      setTimeout(() => {

        this.quantity.nativeElement.focus();
        this.quantity.nativeElement.select();
      }, 0);
    }
    this.setSubTotal()
  }
  /**Thm sn phm v bill detail */
  public addInvoiceDetailMutilLine(value: any) {
    this.invoice_detail.push({
      name: value.name,
      id_product: value._id,
      quantity: 1,
      unit: value.unit,
      units: value.units,
      ratio: value.ratio ? value.ratio : 1,
      ptype: value.type,
      price: value.price_import,
      price_origin: value.price_import,
      id_subproduct: value.id_subproduct,
      lots: value.lots,
      lot_number: value.lot_number,
      id_lotproduct: value.id_lotproduct,
    })

    if (this.auto_active_quantity_input) {
      setTimeout(() => {

        this.quantity.nativeElement.focus();
        this.quantity.nativeElement.select();
      }, 0);
    }
    this.setSubTotal()

  }
  auto_active_quantity_input: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').auto_active_quantity_input;
  @ViewChild('quantity') quantity: ElementRef;
  /** Ly bill dng  to cho database */
  public getCreateUpdateInvoice(bill_type: 9) {
    if (bill_type) this.invoice.bill_type = (bill_type)
    return this.invoice
  }
  /** Ly tt c bill detail  to cho database */
  public getCreateUpdateInvoiceDetail(): VhBillType9DetailAdd[] {
    return this.invoice_detail.map(item => {
      let detail: VhBillType9DetailAdd = {
        id_product: item.id_product,
        price: item.price,
        quantity: item.quantity,
        ptype: item.ptype,
        ratio: item.ratio ? item.ratio : 1,
        price_origin: item.price_origin
      }
      if (item['id_lotproduct']) detail['id_lotproduct'] = item.id_lotproduct
      if (item['id_subproduct']) detail['id_subproduct'] = item.id_subproduct

      return detail
    })
  }
}
