import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhOpeningStockInvoice } from 'src/app/sales/interface/vh-opening-stock-invoice';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-opening-stock',
  templateUrl: './opening-stock.component.html',
  styleUrls: ['./opening-stock.component.scss']
})
export class OpeningStockComponent implements OnInit {

  public searchValue: string = ''
  public invoiceShow: VhOpeningStockInvoice[] = []
  public invoice: VhOpeningStockInvoice[];
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  listFilter: Array<any> = [{ value: 3, name: "Low to High value" }, { value: 4, name: "High to Low value" }]
  tableHeight: string;
  constructor(
    private vhQuery: VhQuery,
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private vhAuth: VhAuth,
    private cdRef: ChangeDetectorRef
  ) {
  }

  ngOnInit() {
    if (this.dataRestore) {
      this.searchValue = this.dataRestore.searchValue
      this.filterType = this.dataRestore.filterType 
    }
    else { this.filterType = 3;  }
    this.getInvoice(this.filterType)
  }
  

  /**
   * lc bill theo bill_code hoc tn nv to bill
   * @param value 
   * @example this.searchBill('')
   */
  searchBill(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.invoiceShow = this.vhAlgorithm.searchList(val, this.invoice, ['bill_code', 'employee_name'])
    } else this.invoiceShow = [...this.invoice]
  }

  /**
   * get danh sch ha n khch tr t DB
   * @example this.getInvoiceToday(1)
   */
  getInvoice(filterType?) {
    this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => {
      this.vhQuerySales.getBills_byFields( { bill_type: { $eq: 9 }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id } }, {})
        .then((invoice: VhOpeningStockInvoice[]) => {
          this.invoice = invoice.map(item => {
            return { ...item, employee_name: this.vhAuth.getlocalEmployee(item.id_employee).name }
          })
          
          this.vhComponent.hideLoading(0)
          if (filterType) this.sortFilter(filterType)
          else this.searchBill(this.searchValue)
        }).catch(() => this.vhComponent.hideLoading(0))
    })
  }
  
  /**
   * router qua trang to bill
   * @example this.goToCart()
   */
  goToCart() {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType }
    this.router.navigate(["/sales/dashboard/manage/invoice/opening-stock/add-opening-stock"], { state: { dataRestore } })
  }

  /**
   * router qua trang to bill
   * @example this.goToCart()
   */
  goToDetail(id) {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType }
    this.router.navigate(["/sales/dashboard/manage/invoice/opening-stock/detail"], { state: { id, dataRestore } })
  }

  public filterType: 3 | 4 = 3;
   
  /**
   * lc ha n theo date hoc total
   * @example this.sortFilter(3)
   */
  sortFilter(filterType: 3 | 4) {
    switch (filterType) {
      case 3: this.invoice.sort((prev, next) => (prev.total * (1 + prev.tax / 100)) - (next.total * (1 + next.tax / 100)));   break;
      case 4: this.invoice.sort((prev, next) => (next.total * (1 + next.tax / 100)) - (prev.total * (1 + prev.tax / 100)));  break;
    }

    this.searchBill(this.searchValue)
  }
  
  ngAfterViewChecked() {
    if (document.querySelector("#supplier-return-invoice-today") && document.querySelector(".ant-table-thead") && document.querySelector(".supplier-return-invoice-today-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#supplier-return-invoice-today").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".supplier-return-invoice-today-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 80 + "px";
    }
    this.cdRef.detectChanges();
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/invoice"])
  }
  /**
   * router qua trang to bill bng excel
   * @example this.goToCart()
   */
  gotoCreateDataOpening() {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType }

    this.router.navigate(["/sales/dashboard/manage/invoice/opening-stock/create-opening-data"], { state: dataRestore })
  }

}
